import { Action } from '@ali/4ever-cangjie';
import TableRow from '../../mo/models/tableRow';

export const TABLE_INSERT_ROW = Symbol('TABLE_INSERT_ROW');

export function insertTableRow(tableRow, type) {
  return {
    type: TABLE_INSERT_ROW,
    payload: {
      node: tableRow,
      type,
    },
  };
}

interface TableInsertRowPayload {
  node: TableRow;
  type: string;
}

interface TableInsertRowAction {
  type: typeof TABLE_INSERT_ROW;
  payload: TableInsertRowPayload;
}

export function isTableInsertRowAction(
  action: Action,
): action is TableInsertRowAction {
  return action.type === TABLE_INSERT_ROW;
}
